@import url(https://fonts.googleapis.com/css?family=Karla:400,700&display=swap);
:root {
  --primary-dark: rgba(55, 71, 79, 1);
  --primary-light: rgba(96, 125, 139, 1);
  --primary-blue: rgba(179, 229, 252, 1);
  --primary: #6a67ce;
}

html {
