  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container.results {
  margin-bottom: 2rem; }
  @media screen and (max-width: 540px) {
    .container.results {
      margin: 3rem auto; } }

.container.recap {
  position: absolute;
  height: 500px;
  margin-bottom: 5rem;
  overflow-y: scroll;
  justify-content: flex-start;
  background: rgba(60, 76, 94, 0.1);
  border-color: rgba(255, 255, 255, 0.1);
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  opacity: 0.5; }
  .container.recap:hover {
    opacity: 1; }
  @media screen and (max-width: 540px) {
    .container.recap {
      position: relative;
      margin-bottom: 2rem;
      overflow-y: visible;
      height: auto;
      opacity: 1; } }

.container.recap::-webkit-scrollbar-track {
  background-color: rgba(60, 76, 94, 0.2); }

.container.recap::-webkit-scrollbar {
  width: 6px; }

.container.recap::-webkit-scrollbar-thumb {
  border-radius: 13px;
  background: rgba(255, 255, 255, 0.2);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  position: relative;
  margin-left: 2rem; }

.container.recap ul li {
  padding: 10px 15px;
  display: flex;
  align-items: center;
  height: 35px; }

.container.recap ul li > span {
  padding-left: 8px; }

.container.recap ul li label {
  display: none; }

.container.recap ul li.user-answer label {
  display: flex; }

.radio.recap {
  padding: 0;
  display: inline-block; }

.question-box h2 {
  color: white; }

.question-box ul {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0; }

.container.questions .question-box ul {
  margin-bottom: 2rem; }

.question-box ul li,
.categories li {
  cursor: pointer;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: calc(50% - 5px);
  background: #131e29;
  border: 1px solid #3c4c5e;
  color: white;
  font-size: 1.1rem;
  border-radius: 3px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  margin-bottom: 10px; }
  .question-box ul li:hover,
  .categories li:hover {
    background: #3d383a;
    border-color: #f29559; }
  .question-box ul li:active, .question-box ul li:focus,
  .categories li:active,
  .categories li:focus {
    background: #3d383a;
    border-color: #f29559; }
  @media screen and (max-width: 570px) {
    .question-box ul li,
    .categories li {
      flex-basis: 100% !important;
      font-size: 0.9rem !important; } }

.question-box ul li span {
  display: flex;
  align-items: center; }

.question-box ul li input[type="radio"] {
  margin-right: 10px;
  background: red; }

.radio {
  padding: 10px 15px;
  display: block;
  cursor: pointer; }
  .radio input {
    display: none; }
    .radio input + span {
      line-height: 15px;
      height: 15px;
      padding-left: 15px;
      display: block;
      position: relative; }
      .radio input + span:not(:empty) {
        padding-left: 23px; }
      .radio input + span:before, .radio input + span:after {
        content: "";
        width: 15px;
        height: 15px;
        display: block;
        border-radius: 50%;
        left: 0;
        top: 0;
        position: absolute; }
      .radio input + span:before {
        background: #d1d7e3;
        -webkit-transition: background 0.2s ease, -webkit-transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 2);
        transition: background 0.2s ease, -webkit-transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 2);
        transition: background 0.2s ease, transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 2);
        transition: background 0.2s ease, transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 2), -webkit-transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 2); }
      .radio input + span:after {
        background: #fff;
        -webkit-transform: scale(0.78);
                transform: scale(0.78);
        -webkit-transition: -webkit-transform 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.4);
        transition: -webkit-transform 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.4);
        transition: transform 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.4);
        transition: transform 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.4), -webkit-transform 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.4); }
    .radio input:checked + span:before {
      box-shadow: 0px 0px 10px 0px #f29659;
      -webkit-transform: scale(1.04);
              transform: scale(1.04);
      background: #f29559; }
    .radio input:checked + span:after {
      -webkit-transform: scale(0.4);
              transform: scale(0.4);
      -webkit-transition: -webkit-transform 0.3s ease;
      transition: -webkit-transform 0.3s ease;
      transition: transform 0.3s ease;
      transition: transform 0.3s ease, -webkit-transform 0.3s ease; }
  .radio:hover input + span:before {
    -webkit-transform: scale(0.92);
            transform: scale(0.92); }
  .radio:hover input + span:after {
    -webkit-transform: scale(0.74);
            transform: scale(0.74); }
  .radio:hover input:checked + span:after {
    -webkit-transform: scale(0.4);
            transform: scale(0.4); }

body {
